.main {
    position: relative;
    height: 100%;
    width: 100%;
    max-width: 412px;
    padding: 0;
}

/*==================BACKGROUND===================*/
.home-bg {
    z-index: -1;
    height: 100%;
    width: 99%;
    font-size: 0;
    position: absolute;
}

.videoTag {
    object-fit: cover;
    width: 100%;
    height: 33.33%;
}

.home-bg-gradient {
    z-index: 1;
    height: 100%;
    width: 100%;
    position: absolute;
    background-image: linear-gradient(transparent, black);
    opacity: 0.5;
}


/*==================FOREGROUND===================*/

.home-content {
    height: 92%;
    z-index: 3;
    text-align: center;
    padding: 16px;
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: center;
    gap: 16px;
}

.home-content-header-opacity-none, .home-content-details-opacity-none{
    opacity: 0;
    transition: all 1s ease-in-out;
}

.home-content-header-opacity-full, .home-content-details-opacity-full{
    opacity: 1;
    transition: all 1s ease-in-out;
}


.home-content-details{
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: center;
    gap: 8px;
}

.date-time-section {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 16px;
}

.date-time-first, .date-time-last{
    width: 60px;
    position: relative;
}

.date-time-first::after, .date-time-last::after {
    content: "";
    position: absolute;
    left: 0;
    height: 16px;
    width: 100%;
    border-top: 1px solid white;
}

.date-time-first::before, .date-time-last::before {
    content: "";
    position: absolute;
    height: 16px;
    left: 0;
    width: 100%;
    border-top: 1px solid white;
}

@media (min-width: 375px) {
    .date-time-first, .date-time-last{
        width: 80px;
    }
}

@media (min-width: 412px) {
    .date-time-first, .date-time-last{
        width: 90px;
    }
}