@import url('https://fonts.googleapis.com/css2?family=Khula:wght@400;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800&family=Khula:wght@400;600&family=Martel+Sans:wght@300;400;600;700&family=Montserrat:wght@700&family=Oleo+Script&family=Roboto:wght@300;400;500;700&family=Work+Sans:wght@200;300;400;500;600&display=swap');

body {
  margin: 0;
  padding: 0;
  font-family: 'Khula', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  background-color: rgb(53, 53, 53);
}

img {
  height: 48px;
}

h1,
h2 {
  font-size: 26px;
  font-family: 'Oleo Script', system-ui;
  margin-top: -10px;
  margin-bottom: -10px;
  font-weight: 600;
}

h3 {
  margin-top: 0px;
  margin-bottom: 0px;
  font-size: 20px;
  font-weight: 700;
}

h4 {
  margin-top: 0px;
  margin-bottom: 0px;
  font-size: 16px;
  font-weight: 600;

}

h5 {
  margin-top: 0px;
  margin-bottom: 0px;
  font-size: 12px;
  line-height: 14px;
  font-weight: 600;
  
}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
}

@media (min-width: 375px) {

  h1,
  h2 {
    font-size: 32px;
  }

  h3 {
    font-size: 24px;
  }

  h4 {
    font-size: 18px;
  }

  h5 {
    font-size: 14px;
    line-height: 16px;
  }
}

@media (min-width: 412px) {

  h1,
  h2 {
    font-size: 36px;
  }

  h3 {
    font-size: 26px;
  }

  h4 {
    font-size: 20px;
  }

  h5 {
    font-size: 16px;
    line-height: 16px;
  }
}